.wrap_product ul {
    padding: 0;
    margin: 0;
    list-style: none;
}

.wrap_product ul:after {
    display: block;
    clear: both;
    content: "";
}

.wrap_product ul li {
    display: inline-flex;
    text-align: center;
    padding: 4px;
    vertical-align: middle;
    width:20%;
