}

.wrap_product ul li a, .wrap_product ul li button {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 1;
    flex-wrap: wrap;
    background-color: #fff !important;
    height: 160px;
    width: 100%;
    border-radius: 6px;
    text-decoration: none;
    border-width: 1.5px;
    border-style: solid;
    border-color: #fff;
    -webkit-box-shadow: 0 0 10px 0 rgb(82 63 105 / 15%);
    box-shadow: 0 0 10px 0 rgb(82 63 105 / 15%);
    -webkit-transition: border 500ms ease-out;
    -moz-transition: border 500ms ease-out;
    -o-transition: border 500ms ease-out;
    transition: border 500ms ease-out;
}

.wrap_product ul li img {
    width: 50px;
}

.wrap_product ul li span {
    display: block;
    margin-top: 15px;
    color: #333;
    font-weight: 600;
    line-height: 16px;
}

